import { Behavior } from "../../src/classes/behavior";

import { ISliderOptions } from "./interfaces/slider-options.interface";
import { ISliderChildren } from "./interfaces/slider-children.interface";

export class SliderBehavior extends Behavior {
	public static behaviorName = 'slider';

	protected defaultOptions: ISliderOptions = {
		slideWidth: 0,
		useArrows: true,
		useDots: true,
		draggable: true,
		infinity: true,
		center: true,
		autoplay: 5000,
		autoplayOnHover: false,
		autoplayOnFocus: true,
		adaptiveHeight: true
	};

	public options: ISliderOptions;
	public children: ISliderChildren;

	public init(): void {
		this.build();
	}

	public build(): void {
		const sliderTrack = document.createElement('DIV'),
			  sliderList = document.createElement('DIV');

		sliderTrack.classList.add('bSlider__track');
		sliderList.classList.add('bSlider__list');

		this.element.classList.add('bSlider');

		this.children.slides = [].slice.call(this.element.children);

		this.children.sliderTrack = sliderList.insertAdjacentElement('beforeend', sliderTrack) as HTMLElement;
		this.children.sliderList = this.element.insertAdjacentElement('beforeend', sliderList) as HTMLElement;
		
		for(const child of this.children.slides) {
			this.children.sliderTrack.insertAdjacentElement('beforeend', child);
		}

		if(this.options.useArrows) {
			// todo
		}

		if(this.options.useDots) {
			// todo
		}


	}

	public destroy() {

	}
 }
